import React, { Component } from 'react';
import FormError from './FormError';
import { validateInput } from '../utils';

class CustomerInfo extends Component {
    constructor(props) {
        super(props);
        this.state = {
            fullname: {
                value: '',
                isInputValid: true, 
                errorMessage: ''
            }, 
            phonenumber:  {
                value: '',
                isInputValid: true, 
                errorMessage: ''
            }, 
        }
    }

    handleInput = event => {
        const { name, value } = event.target;
        // const newState = Object.assign({}, this.state[name]); 
        const newState = {...this.state[name]};
        newState.value = value;
        this.setState({[name]: newState});
    }

    handleInputValidation = event => {
        const { name } = event.target;
        const { isInputValid, errorMessage } = validateInput(name, this.state[name].value);
        const newState = {...this.state[name]};
        newState.isInputValid = isInputValid;
        newState.errorMessage = errorMessage;
        this.setState({[name]: newState})
    }

    render() {
        return (
            <div className="customer-info">
                <form>
                    <input type="radio" name="gender" defaultValue="male" defaultChecked />Anh
                    <input type="radio" name="gender" defaultValue="female" />Ch
                    <div className="typing-input">
                        <input
                            type="text"
                            className="input-field"
                            name="fullname"
                            placeholder="H v tn"
                            onChange={this.handleInput}
                            onBlur={this.handleInputValidation}
                            required />
                        <FormError
                            type="fullname"
                            isHidden={this.state.fullname.isInputValid} 
                            errorMessage={this.state.fullname.errorMessage} />
                        <input
                            type="text"
                            name="phonenumber"
                            className="input-field"
                            placeholder="S in thoi"
                            onChange={this.handleInput}
                            onBlur={this.handleInputValidation}
                            required />
                        <FormError 
                            type="phonenumber"
                            isHidden={this.state.phonenumber.isInputValid} 
                            errorMessage={this.state.phonenumber.errorMessage} />
                        <input type="text" name="others" className="input-field" placeholder="Yu cu khc (khng bt buc)" />
                    </div>
                    <p> c giao hng nhanh hn, hy chn thm</p>
                    <input type="radio" name="deliveryAddress" defaultValue="home" defaultChecked />
                    <span className="deliveryAddress">a ch giao hng</span>
                    <input type="radio" name="deliveryAddress" defaultValue="store" />
                    <span className="deliveryAddress">Nhn ti siu th</span>
                    <br />
                    <div className="payment-button">
                        <a href="" className="payment-onDelivery">
                            <div className="button-content">
                                THANH TON TI NH
                        <br />
                                <span>Xem hng trc, khng mua khng sao</span>
                            </div>
                        </a>
                        <a href="" className="payment-online">THANH TON ONLINE
                        <br />
                            <span>Bng th ATM, Visa, Master</span>
                        </a>
                    </div>
                </form>

            </div>
        )
    }

}

export default CustomerInfo;


